import * as api from './servises/api';

// МОДЕЛЬ ЗАМЕТОК С МЕТОДАМИ РОБОТИ С НИМИ
// notes - масив для отрисовки интерфейса
class Notepad {
  constructor(notes = []) {
    this.notes = notes;
  }
  get() {
    return api.getPost().then(posts => {
      console.log(posts);
      this.notes = posts;
      console.log(this.notes);

      // console.log(this.notes);
      return this.notes;
    });
  }

  add(item) {
    return api.savePost(item).then(saveItem => {
      this.notes.push(saveItem); // добавим в заметки для дальнейшой отрисовки интерфейса
      console.log(this.notes);
      return saveItem;
    });
  }
  delete(id) {
    return api.deletePost(id).then(() => {
      this.notes = this.notes.filter(item => item.id !== id);
      console.log(this.items);
      return id;
    });
  }

  updatePost(id, post) {
    return api.updatePost(id, post).then(updateitem => {
      this.notes.map(item => (item.id === updateitem.id ? updateitem : item));
    });
  }
}

export default Notepad;
